<style scoped>
.layout{
    position: relative;
    overflow: hidden;
    height: 100%;
    margin-top: 100px;
}
.ivu-layout {
  background: none;
}
.layout-nav{
    width: 420px;
    margin: 0 auto;
    margin-right: 20px;
}
.layout-footer-center{
    text-align: center;
}
.logs {
  max-height: 500px;
  overflow: auto;
}
.lock-view {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 999;
  padding-top: 15%;
}
.lock-view .content {
  z-index: 99;
  width: 1024px;
  margin: 0 auto;
}
.lock-view .mask {
  position: absolute;
  width: 100%;
  height: 100%;
  background:white;
  opacity: 0.95;
  top: 0;
  left: 0;
  z-index: -1;
}
</style>
<template>
    <div class="layout">
        <Layout>
            <Content :style="{padding: '0 50px'}">
                <Card :bordered="false" :shadow="true">
                    <Spin size="large" fix v-if="!loaded"></Spin>
                    <div class="lock-view" v-if="lock">
                      <div class="mask"></div>
                      <div class="content">
                        <Alert show-icon><Icon type="md-lock" slot="icon"></Icon>请输入管理密码进行操作</Alert>
                        <Input type="password" v-model="password" placeholder="密码"></Input>
                        <Divider />
                        <Button type="success" icon="md-unlock" long @click="requestToken">解锁</Button>
                      </div>
                    </div>
                    <Row :gutter="16">
                        <Col span="8">
                          <Spin size="large" fix v-if="connecting"></Spin>
                          <Card :bordered="false" :dis-hover="true">
                            <p slot="title">系统状态</p>
                            <CellGroup>
                                <Cell title="服务器地址" :label="config.host" :selected="connectStatus ? true : false" />
                                <Cell :class="isConnected ? 'connected' : 'not-connect'" title="连接状态" :extra="connectStatus" />
                                <Cell title="平均延迟" :extra="connectPing" />
                                <Cell title="固件版本" :extra="version" />
                            </CellGroup>
                            <Divider />
                            <Button type="error" v-if="isConnected == 1" long @click="stopConnect">断开连接</Button>
                            <Button type="success" v-if="isConnected == 0" long @click="doConnect" :loading="connecting">发起连接</Button>
                            <br />
                            <br />
                            <Button type="info" long @click="speedTest" :loading="speedTesting">测速<template v-if="speedTesting">中</template></Button>
                          </Card>
                          <Card :bordered="false" :dis-hover="true">
                            <p slot="title">系统配置</p>
                            <Form :label-width="100">
                              <FormItem label="远程服务器地址">
                                <Input v-model="config.host" placeholder="服务器地址" :disabled="isRealConnected"></Input>
                              </FormItem>
                              <FormItem label="学校编号">
                                <Input v-model="config.schoolid" placeholder="学校编号" :disabled="isRealConnected"></Input>
                              </FormItem>
                              <FormItem label="认证校验码">
                                <Input v-model="config.code" type="password" placeholder="认证校验码" :disabled="isRealConnected"></Input>
                              </FormItem>
                              <FormItem label="自动连接">
                                  <i-switch v-model="config.autostart" size="large" :disabled="isRealConnected">
                                      <span slot="open">是</span>
                                      <span slot="close">否</span>
                                  </i-switch>
                              </FormItem>
                              <FormItem v-if="!isRealConnected">
                                <Button type="primary" @click="saveConfig" :loading="saveing">保存</Button>
                              </FormItem>
                              <FormItem v-else>
                                <Button type="error" @click="stopConnect">断开连接修改</Button>
                              </FormItem>
                            </Form>
                          </Card>
                        </Col>
                        <Col span="16">
                          <Card :bordered="false" :dis-hover="true">
                            <p slot="title">系统日志</p>
                            <div class="logs" ref="logs">
                              <div class="log" v-for="(log, index) in logs" :key="index">
                                <Alert show-icon banner :type="log.type">
                                  {{date(log.time)}} [{{log.target}}] {{log.message.text}} <a class="detail" v-if="log.message.body" @click="showLogDetail(log)">详情</a>
                                </Alert>
                              </div>
                            </div>
                          </Card>
                        </Col>
                    </Row>
                </Card>
            </Content>
        </Layout>
        <Modal v-model="showDetail" fullscreen title="详情">
            <tree-view :data="logDetail"></tree-view>
            <Divider />
            {{JSON.stringify(logDetail)}}
        </Modal>
    </div>
</template>
<script>
import * as moment from 'moment';
export default {
  data() {
    return {
      lock: false,
      password: '',
      showDetail: false,
      logDetail: {},
      loaded: false,
      speedTesting: false,
      saveing: false,
      connecting: false,
      logs: [],
      maxLog: 100,
      isConnected: '',
      pong: '',
      version: '',
      config: {}
    };
  },
  computed: {
    isRealConnected() {
      return this.isConnected == 1;
    },
    connectStatus() {
      if (this.isConnected === '') {
        return '';
      }
      return this.isConnected ? '已连接' : '未连接';
    },
    connectPing() {
      return this.isRealConnected && this.pong ? this.pong + 'ms' : '...';
    }
  },
  methods: {
    showLogDetail(log = {}) {
      const body = log.message.body || {};
      this.logDetail = body;
      this.showDetail = true;
    },
    log(message, target = 'web', type = 'info', time = 0) {
      if (this.logs.length >= this.maxLog) {
        this.logs.splice(0, 1);
      }
      message = message instanceof Object ? message : {
        text: message,
        body: null
      };
      time = time || Math.round(new Date().getTime() / 1000);
      const log = {
        message,
        target,
        type,
        time
      };
      this.logs.push(log);
      this.toBottom();
    },
    toBottom() {
      setTimeout(() => {
        const dom = this.$refs.logs;
        dom.scrollTop += dom.scrollHeight;
      }, 300);
    },
    stopConnect() {
      this.$root.socket.emit('remote/disconnect');
    },
    doConnect() {
      this.connecting = true;
      this.$root.socket.emit('remote/connect');
      this.log('发起连接请求', 'web');
    },
    date(time) {
      return moment.unix(time).format('YYYY-MM-DD HH:mm:ss');
    },
    speedTest() {
      this.speedTesting = true;
      this.$root.socket.emit('speed/test', null, result => {
        this.speedTesting = false;
      });
    },
    saveConfig() {
      this.saveing = true;
      this.$root.socket.emit('system/save/config', this.config, result => {
        this.saveing = false;
        this.$Message.success('操作成功');
      });
    },
    requestToken() {
      this.$root.socket.emit('system/get/token', this.password, result => {
        if (result.error) {
          this.$Message.error(result.data);
        } else {
          window.localStorage.setItem('token', result.data);
          this.lock = false;
        }
      });
    }
  },
  mounted() {
    this.$root.socket.on('log', log => {
      this.log(log.message, log.target, log.type, log.time);
    });
    this.$root.socket.on('disconnect', () => {
      this.isConnected = 0;
      this.log('本地服务已断开', 'web', 'error');
    });
    this.$root.socket.on('connect', () => {
      this.log('本地服务已连接', 'web', 'success');
    });
    this.$root.socket.on('remote/pong', pong => {
      this.pong = pong;
    });
    this.$root.socket.on('remote/connect', () => {
      this.connecting = false;
      this.isConnected = 1;
    });
    this.$root.socket.on('remote/disconnect', () => {
      this.connecting = false;
      this.isConnected = 0;
    });
    this.$root.socket.emit('query/connect', null, json => {
      this.isConnected = json.data;
    });
    this.$root.socket.emit('query/system', {
      token: window.localStorage.getItem('token')
    }, json => {
      this.lock = json.data.lock;
      this.speedTesting = json.data.speedTesting;
      this.isConnected = json.data.isConnected;
      this.pong = json.data.lastPong;
      this.version = json.data.version;
      this.config = json.data.config;
      this.loaded = true;
    });
    this.$root.socket.emit('query/logs', null, json => {
      this.logs = json.data;
    });
  }
};
</script>



